import React, { useRef } from 'react'
import { connect } from 'react-redux'
import { CSSTransition } from 'react-transition-group'

import { setSearchedSerials } from './../../store/CardList/action'


const Popup = (props) => {

  const popup = useRef(null)

  const closePopup = (e) => {
    if (e.target.classList.contains('popup')) {
      props.setSearchedSerials(null, false)
    }
  }

  return (
    <CSSTransition
      in={props.searchedList.state}
      classNames="popup"
      nodeRef={popup}
      timeout={1000}
    >
      <div className="popup"
        ref={popup}
        onClick={closePopup}
      >
        <div className="popup__container">
          <p className="popup__text">
            Oooops... Sorry we didn't find nothing for your request. Try again!
          </p>
          <button className="popup__btn"
            onClick={() => props.setSearchedSerials(null, false)}
          ></button>
        </div>
      </div>
    </CSSTransition >
  )
}

const mapStateToProps = (state) => {
  return {
    searchedList: state.dataOfSerials.searchedList
  }
}

const mapDispatchToProps = {
  setSearchedSerials
}

export default connect(mapStateToProps, mapDispatchToProps)(Popup)